import React, { useState } from 'react';
import ConfettiExplosion from 'react-confetti-explosion';
import './WinPage.css';
import { useNavigate } from 'react-router-dom';

const cards = [
  {
    id: 1,
    name: ',   ?',
    image: 'assets/picnic.jpg',
    menu: ['', ' ', ' ', ' ', ''],
    locations: ['', '  ', '  '],
    entertainment: [' ', '   ', '   ']
  },
  {
    id: 2,
    name: ' (-)',
    image: 'assets/burgers.jpg',
    menu: ['', ' ', ' -'],
    locations: ['', ' ', ' True Burger Bar'],
    entertainment: ['', ' ', '  ', '   ']
  },
  {
    id: 3,
    name: '     ',
    image: 'assets/home.jpg',
    menu: ['', ' ', ' -'],
    locations: ['', '  ', ' '],
    entertainment: ['12   ', ' I love Steam Deck more than everything', '  ']
  },
  {
    id: 4,
    name: '  Dream-town',
    image: 'assets/dream-town.jpg',
    menu: ['', ' ', ' '],
    locations: ['Dream town'],
    entertainment: ['/', '  ', ' ', ' ']
  }
];

export const WinPage = () => {
  const [isExploding, setIsExploding] = useState(true);
  const [selectedCard, setSelectedCard] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [isClicked, setIsClicked] = useState(false);
  const navigate = useNavigate();

  const handleCloseModal = () => {
    setShowModal(false);
  };

  const handleCardClick = (card) => {
    setSelectedCard(card);
    setShowModal(true);
    setIsClicked(true);
  }

  return (
    <div className='win-page'>
      <h2>!  ,</h2>
      <h2> ?</h2>
      {isExploding && <ConfettiExplosion />}
      <div className="cards">
        {cards
          .sort(() => Math.floor(Math.random() * (3 - 0) + 0))
          .map((card) => (
            <div
              key={card.id}
              onClick={() => handleCardClick(card)}
              className={`card-win`}
            >
            </div>
          ))}
      </div>
      {isExploding && <ConfettiExplosion />}
      {selectedCard ? (
        <div className="card-container">
          {showModal && (
            <div className="modal">
              <div className="modal-content">
                <button className="close-button" onClick={handleCloseModal}>
                  X
                </button>
                <h2>{selectedCard.name}</h2>
                <div>
                  <img src={selectedCard.image} alt="card" className='card-win-image' />
                  <div>
                    <p>{`: ${selectedCard.menu}`}</p>
                    <p>{`: ${selectedCard.locations}`}</p>
                    <p>{`: ${selectedCard.entertainment} `}</p>
                    <br />
                    <pre>*    ,        :)</pre>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      ) : null}

      {isClicked &&
        <div className='button-class'>
          <h1
            className='next'
            onClick={() => { navigate('/wish') }}
          >
            Next!
          </h1>
        </div>
      }
    </div>
  )
}